.Home{
/*	border-bottom: 0.6px solid lightgray;*/
/*	padding-bottom: 3%;*/
	width: 100vw;
	height: 86vh;
	/* background-color: #f9dec9; */
	
	display: flex;
	overflow: scroll;
align-items: center;
	color: black ;

}

.container{
	height: 77vh;
    width: 80vw;
    margin-left: 10vw;
    text-align: left;
    padding-bottom: 2vh;
    /* overflow: scroll; */
/*   margin-top: 1vh;*/
    padding-top: 2vh;
	display: flex;
	justify-content: center;
	column-gap: 10px;
}

.imgHome{
	width: 500px;
}

.leftHome{
	width: 15cm;
	height: 77vh;
}


.bodyTextH{

	font-family: Lora;
	font-size: 40px;
	/* height: 7vh; */

	overflow: hidden;
	white-space: nowrap;
	
	margin: 0px 10px;
	padding-top: 2vh;
	animation: typing 3.5s steps(40, end);
	
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
.bodyTextHome{
	margin: 0px 0px;
}
.bodyTextHomeSub{
	margin: 10px 0px;
}
.bodyTextHomeSubH{
	margin: 10px 0px;
}
.bodyText{
/*	font-family: monospace;*/


	overflow: hidden;
	
	font-size: 25px;
	margin:10px 10px;
	height: 65vh;
	
}
.build{
	font-size: 25px;
	border-radius: 8px;
	border: none;
	height: 60px;
	width: 250px;
	background-color: #5fbff9;
	cursor: pointer;
	color: black;
  box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
  margin: 15px 0px;
}

.build:hover{
	transition: 1.2s;
	transform: scale(1.2);
}

@media  screen and (max-width:1200px) {
	.imgHome{
		width: 400px;
	}
	.bodyText{
		font-size: 20px;
	}
}
@media screen and (max-width:950px){
	.imgHome{
		display: none;
	}
}

